.dialogs{
  display: grid;
  grid-template-columns: 2fr 8fr;
  grid-template-rows: 1fr 1fr;
  font-family: "Roboto Light", sans-serif;
  color: #18507a;
  align-items: start;
}

.dialogsItems{
  padding: 10px;
}

.dialog{
  color: white;
  display: block;
}

.messages{
  padding: 10px;
}
