.nav {
  grid-area: n;
  background-color: #B39CD0;
  border-radius: 30px;
}

.nav a {
  display: block;
  color: white;
  font-size: 18px;
  font-weight: 200;
  font-family: "Cambria Math", serif;
  margin: 20px 0;
}

.nav li a:hover {
  opacity: .6;
}

.active {
  color: #ffdb29;
}

.friends h2{
  padding-left: 40px;
}

.avatars{
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.avatars img {
  width: 70px;
  height: 70px;
}